import React from 'react';
import cartStorage from '../../backend/js/shop.cartstorage';
import { indicators, textblocks, formatcash } from '../../backend/js/shop.config';
import './shopping-summary.css';

//TODO: textblocks
//TODO: check user stories!

//TODO: pipe articles as props like user -> get rid of ref
const ShoppingSummary = (props) => {
  return(
    <React.Fragment>
      <CartSummary ref={(ref) => {window.cartSummary = ref}} />
      <DataSummary user={props.user} />
      <PaymentmMethods />
    </React.Fragment>
  );
}

//TODO: abstract
//TODO: id to class
class CartSummary extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      articles: [],
    }
  }

  syncCartSummaryWithStorage(){
    this.setState({
      articles: JSON.parse(window.localStorage.cart_storage),
    });
  }

  render() {
    const { vatRate, vatIncluded, shipping, shippingThreshhold } = indicators;
    const { incl, plus, vat } = textblocks;
    const shippingEval = cartStorage.total() < shippingThreshhold ? shipping : 0;
    const value = cartStorage.netValue();
    const valueFinal = Number(vatIncluded ? value : cartStorage.grossSum()) + shippingEval;
    const vatIndicator = `${vatIncluded ? incl : plus} ${vatRate*100}% ${vat}`;
    return(
      <div id='cartSummary'>
        <EmptyLine />
        <HeaderLine content="Artikel" />
        {this.state.articles.map(article =>
          <div key={article.id} className='summary-row'>
            <div className='summary-element cell left'>
              {article.name}
            </div>
            <div className='summary-element cell right'>
              {formatcash(article.qty*article.price)}
            </div>
            <div className='summary-element line left top small'>
              <span className='colored'>{formatcash(article.price)}</span>
              <span>(Anzahl: {article.qty})</span>
            </div>
          </div>
        )}
        <EmptyLine />
        <div className='summary-row balancing'>
          <div className='summary-element multicell left white'>
            Zwischenbetrag
          </div>
          <div className='summary-element cell right white'>
            {formatcash(value)}
          </div>
        </div>
        <div className='summary-row balancing'>
          <div className='summary-element multicell left white'>
            {vatIndicator}
          </div>
          <div className='summary-element cell right white'>
            {formatcash(cartStorage.vatAmount())}
          </div>
        </div>
        <div className='summary-row balancing'>
          <div className='summary-element multicell left white'>
            Versandkosten
          </div>
          <div className='summary-element cell right white'>
            {formatcash(shippingEval)}
          </div>
        </div>
        <div className='summary-row balancing'>
          <div className='summary-element balancing multicell left white bg'>
            Gesamtbetrag
          </div>
          <div className='summary-element balancing cell right white bg'>
            {formatcash(valueFinal)}
          </div>
        </div>
        <EmptyLine />
      </div>
    );
  }
}

//TODO: id to class
const DataSummary = (props) => {
  const {
    fname,
    lname,
    address,
    zip,
    city,
    country,
    email,
    fnameInvoice,
    lnameInvoice,
    addressInvoice,
    zipInvoice,
    cityInvoice,
    countryInvoice,
    isInvoiceAddress
  } = props.user;
  const name = `${fname} ${lname}`;
  const location = `${zip} ${city}`;
  const nameInvoice = isInvoiceAddress &&
    `${fnameInvoice} ${lnameInvoice}`;
  const locationInvoice = isInvoiceAddress &&
    `${zipInvoice} ${cityInvoice}`;
  const likeShipping = !isInvoiceAddress ? '(wie Lieferadresse)' : null;
  return(
    <div id='dataSummary'>
      <HeaderLine content="Lieferadresse" />
      <AddressLine content={name} />
      <AddressLine content={address} />
      <AddressLine content={location} />
      <AddressLine content={country} />
      <HeaderLine content="E-Mail-Kontakt" />
      <AddressLine content={email} />
      <HeaderLine content="Rechnungsadresse" supplement={likeShipping} />
      <AddressLine content={nameInvoice} />
      <AddressLine content={addressInvoice} />
      <AddressLine content={locationInvoice} />
      <AddressLine content={countryInvoice} />
    </div>
  );
}

const EmptyLine = () => {
  return(
    <div className='summary-row address'>
      <div className='summary-element line'></div>
    </div>
  );
}

const HeaderLine = (props) => {
  const { content, supplement } = props;
  if(content) {
    return(
      <div className='summary-row header'>
        <div className='summary-element line'>
          {content}
          {supplement &&
            <span style={{margin: '0 0 0 5px'}}>
              <i style={{fontWeight: 'normal'}}>{supplement}</i>
            </span>
          }
        </div>
      </div>
    );
  } else {
    return <React.Fragment />;
  }
}

const AddressLine = (props) => {
  const { content } = props;
  if(content) {
    return(
      <div className='summary-row address'>
        <div className='summary-element line'>
          {content}
        </div>
      </div>
    );
  } else {
    return <React.Fragment />;
  }
}

